#OwnCardScreen {
    img.logo {
        max-height: 60px;
        max-width: 100%;
    }

    ons-modal#show-qrcode {
		ons-row.total-row {
			font-size: 25px;
			border-top: 1px solid #ccc;
			padding-top: 12px;
		}

		ons-row.total-row-top-bottom {
			font-size: 15px;
			border-top: 1px solid #ccc;
			border-bottom: 1px solid #ccc;
			padding-top: 10px;
			padding-bottom: 10px;
		}
	}

	ons-modal.show-qrcode-modal {
		padding: 1rem;
		width: 100%;

		div.modal-container {
			padding: 0.6rem;
			background: #fff;
			color: #030303;
			padding: 1rem 0.6rem;
			border-radius: 4px;
		}

		div.modal__content {
			margin: 0px auto;
		}
		
		ons-col {
			margin-bottom: 10px;
			text-align: left;
			padding-left: 0.3rem;
			padding-right: 0.3rem;

			label {
				font-size: 12px;
				margin-left: 2px;
				margin-bottom: 5px;
				color: #424242;
			}
		}

		ons-input {
			width: 100%;
		}
	}
}